  <template>
    <div class="painel">
        <h2 class="painel-titulo" @dblclick="visivel = !visivel"> {{ titulo }} </h2>
        <transition name="painel-fade">
          <div class="painel-conteudo" v-show="visivel">
            <slot></slot>
          </div>
        </transition>
    </div>
</template>

<script>
export default {
  props: ['titulo'],
  data() {
    return {
      visivel: true,
    }
  },
}
</script>
  
<style scoped>
  /* Estilo do painel */
  .painel { padding: 0 auto; border: 2px solid grey; display: inline-block; margin: 5px; box-shadow: 5px 5px 10px grey; width: 200px; height: 100%; vertical-align: top; text-align: center; }
  .painel .painel-titulo { text-align: center; border: 2px solid; background: rgb(195, 217, 250); margin: 0 0 15px 0; padding: 10px; text-transform: uppercase; cursor: pointer; }
  .painel, .painel-titulo { border-top-left-radius: 10px; border-top-right-radius: 10px; }
  *{
    box-shadow: 5px 5px 5px;
  }

  .painel-fade-enter, .painel-fade-leave-active {
    opacity: 0;
  }
  .painel-fade-enter-active, .painel-fade-leave-active {
    transition: opacity 0.5s;
  }

  .painel-conteudo {
    overflow: hidden;
  }

</style>



